export default ()=> {
      return (
        <div className="question" data-index="46" dangerouslySetInnerHTML = {{ __html: `
<h6>47. 输出是什么?</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-const">const</span> person <span class="token operator">=</span> <span class="token punctuation">{</span>
  name<span class="token operator">:</span> <span class="token string">"Lydia"</span><span class="token punctuation">,</span>
  age<span class="token operator">:</span> <span class="token number">21</span>
<span class="token punctuation">}</span><span class="token punctuation">;</span>

<span class="token keyword keyword-for">for</span> <span class="token punctuation">(</span><span class="token keyword keyword-const">const</span> item <span class="token keyword keyword-in">in</span> person<span class="token punctuation">)</span> <span class="token punctuation">{</span>
  console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>item<span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token punctuation">}</span>
</code></pre>
<ul>
<li>A: <code>{ name: &quot;Lydia&quot; }, { age: 21 }</code></li>
<li>B: <code>&quot;name&quot;, &quot;age&quot;</code></li>
<li>C: <code>&quot;Lydia&quot;, 21</code></li>
<li>D: <code>[&quot;name&quot;, &quot;Lydia&quot;], [&quot;age&quot;, 21]</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: B</h4>
<p>在<code>for-in</code>循环中,我们可以通过对象的key来进行迭代,也就是这里的<code>name</code>和<code>age</code>。在底层，对象的key都是字符串（如果他们不是Symbol的话）。在每次循环中，我们将<code>item</code>设定为当前遍历到的key.所以一开始，<code>item</code>是<code>name</code>，之后 <code>item</code>输出的则是<code>age</code>。</p>
</p>
</details>
` }}></div>
      );
    }